import $ from 'jquery';


const init = () => {
  const $reviews = $('.reviews__list');
  $reviews.slick({
    autoplay: true,
    autoplaySpeed: 8000,
    fade: true,
    arrows: false,
    dots: true,
  })
  .on('afterChange', () => {
    $('.reviews__item-author_animate-author')
    .removeClass('reviews__item-author_animate-author');
    $('.reviews__container .slick-active .reviews__item-author')
    .addClass('reviews__item-author_animate-author');

    $('.reviews__item-main_animate-main')
    .removeClass('reviews__item-main_animate-main');
    $('.reviews__container .slick-active .reviews__item-main')
    .addClass('reviews__item-main_animate-main');
  });

  $('.reviews__container .slick-active .reviews__item-author')
  .addClass('reviews__item-author_animate-author');
  $('.reviews__container .slick-active .reviews__item-main')
  .addClass('reviews__item-main_animate-main');

  /*
  const $reviews = $('.reviews__container');
  if ($reviews.length) {
    const reviews = $reviews.flexslider({
      animation: 'slide',
      directionNav: false,
      controlNav: true,
      slideshowSpeed: 10000,
      animationSpeed: 0,
      start: () => {
        $('.reviews__container .flex-active-slide .reviews__item-author')
        .addClass('reviews__item-author_animate-author');
        $('.reviews__container .flex-active-slide .reviews__item-main')
        .addClass('reviews__item-main_animate-main');
      },
      after: () => {
        $('.reviews__item-author_animate-author')
          .removeClass('reviews__item-author_animate-author');
        $('.reviews__container .flex-active-slide .reviews__item-author')
          .addClass('reviews__item-author_animate-author');

        $('.reviews__item-main_animate-main')
          .removeClass('reviews__item-main_animate-main');
        $('.reviews__container .flex-active-slide .reviews__item-main')
          .addClass('reviews__item-main_animate-main');
      },
    });
    $('.flex-control-nav a').on('click', () => {
      setTimeout(() => reviews.flexslider('play'), 6000);
    });
  }
  */
};

module.exports = { init };




