import $ from 'jquery';
import '@fancyapps/fancybox';
import parallaxFigures from '../blocks/parallax_figures/parallax_figures';
import header from '../blocks/header/header';
import slider from '../blocks/slider/slider';
import brands from '../blocks/brands/brands';
import postItem from '../blocks/post_item/post_item';
import reviews from '../blocks/reviews/reviews';
import catalog from '../blocks/catalog/catalog';
import blog from '../blocks/blog/blog';
import article from '../blocks/article/article';
import contactForm from '../blocks/contact_form/contact_form';
import hero from '../blocks/hero/hero';
import preloader from '../blocks/preloader/preloader';


$(() => {
  parallaxFigures.init();
  header.init();
  slider.init();
  brands.init();
  postItem.init();
  reviews.init();
  catalog.init();
  blog.init();
  article.init();
  contactForm.init();
  hero.init();
  preloader.init();

  // $.get('/assets/img/icons/symbol-defs.svg', (svg) => {
  $.get('assets/img/icons/symbol-defs.svg', (svg) => {
    const div = document.createElement('div');
    div.classList.add('icons-holder');
    div.innerHTML = new XMLSerializer().serializeToString(svg.documentElement);
    document.body.insertBefore(div, document.body.childNodes[0]);
  });
});



