import $ from 'jquery';


const init = () => {
  if ($('.catalog').length) {
    $(window).scroll(function cb() {
      const st = $(this).scrollTop();
      const offs = $('.catalog__columns').offset().top;
      const st2 = offs - 800;

      if (st > st2) {
        $('.catalog__kid_boy').css({
          transform: `translateY(-${(st - st2) / 35}%)`,
          // transition: 'transform 1.25s cubic-bezier(0.34, 0.5, 0.04, 0.8)',
        });
        $('.catalog__kid_girl').css({
          transform: `translateY(-${(st - st2) / 25}%)`,
          // transition: 'transform 1.25s cubic-bezier(0.34, 0.5, 0.04, 0.8)',
        });
        $('.catalog__stars-front_boy').css({
          transform: `translateY(-${(st - st2) / 35}%)`,
          // transition: 'transform 1.25s cubic-bezier(0.34, 0.5, 0.04, 0.8)',
        });
        $('.catalog__stars-front_girl').css({
          transform: `translateY(-${(st - st2) / 45}%)`,
          // transition: 'transform 1.25s cubic-bezier(0.34, 0.5, 0.04, 0.8)',
        });
        $('.catalog__stars-back_boy').css({
          transform: `translateY(-${(st - st2) / 40}%)`,
          // transition: 'transform 1.25s cubic-bezier(0.34, 0.5, 0.04, 0.8)',
        });
        $('.catalog__stars-back_girl').css({
          transform: `translateY(-${(st - st2) / 40}%)`,
          // transition: 'transform 1.25s cubic-bezier(0.34, 0.5, 0.04, 0.8)',
        });
        // $('.catalog__content').css({
        //   transform: `translateY(${(st - st2) / 20}%)`,
        // });
      }
    });
  }
};

module.exports = { init };



