import $ from 'jquery';
import 'slick-carousel';


const init = () => {
  const $slider = $('.slider__list');

  if ($slider.length) {
    $slider.slick({
      autoplay: true,
      autoplaySpeed: 8000,
      prevArrow: $('.slider__nav-link_prev'),
      nextArrow: $('.slider__nav-link_next'),
    })
    .on('afterChange', () => {
      $('.slider .slider__slide-info')
      .removeClass('slider__slide-info_animate');
      $('.slider .slick-active .slider__slide-info')
      .addClass('slider__slide-info_animate');
    });

    $('.slider .slick-active .slider__slide-info')
    .addClass('slider__slide-info_animate');
  }
};

module.exports = { init };




