import $ from 'jquery';
import moment from 'moment';


const init = () => {
  moment.locale('ru');

  $.each($('.post__date-label'), (i, node) => {
    const $el = $(node);
    const date = $el.text();
    $el.text(moment(date).format('ll')).css({
      visibility: 'visible',
    });
  });

  $.each($('.album__date-label'), (i, node) => {
    const $el = $(node);
    const date = $el.text();
    $el.text(moment(date).format('ll')).css({
      visibility: 'visible',
    });
  });
};


module.exports = { init };



