import $ from 'jquery';


function init() {
  const $window = $(window);

  $window.on('resize', () => {
    $('.header__nav-toggle').prop('checked', '');
    $('.hamburger').removeClass('is-active');
    $('.header__menu').hide();

    if ($window.outerWidth() >= 992) {
      $('.header__menu').show();
    }
  });

  $('.header__nav-toggle').on('click', function cb() {
    if ($(this).prop('checked')) {
      $('.hamburger').addClass('is-active');
      $('.header__menu').slideDown(400);
    } else {
      $('.hamburger').removeClass('is-active');
      $('.header__menu').slideUp(400);
    }
  });
}


module.exports = { init };



