import $ from 'jquery';
import Masonry from 'masonry-layout';

function init() {
  $(window).on('load', () => {
    if ($('.blog__columns').length) {
      /* eslint-disable */
      const grid = document.querySelector('.blog__columns');
      const msnry = new Masonry(grid, {
        itemSelector: '.post',
      });
      /* eslint-enable */
    }
  });
}

module.exports = { init };



