import $ from 'jquery';
import moment from 'moment';


function init() {
  if ($('.hero__date').length) {
    moment.locale('ru');

    const dateTxt = $('.hero__date-label').text();
    $('.hero__date-label').text(moment(dateTxt).format('ll')).css({
      visibility: 'visible',
    });
  }
}


module.exports = { init };




